import { MyIcon } from "src/core/components"
import React, { useEffect } from "react"
import styled from "styled-components"
import { useTranslation } from "react-i18next"
import {
  Button,
  Card,
  CardBody,
  CardHeader,
  CardText,
  CardFooter,
} from "reactstrap"
import { HConfig, HUtils, HApi } from "src/core/helpers"
import useForceUpdate from "src/core/hook/useForceUpdate"
import Setting2FAHelpers, {
  updateFnUpdateSetting2FAPart,
  updateSettingByResponse,
} from "../helpers"

const WrapIcon = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  background: #e4e6ef;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  margin-right: 0.5rem;
  color: #5e6e82;
`
const Part2FA = props => {
  const { t } = useTranslation()
  const forceUpdate = useForceUpdate()
  const requestSetting = () => {
    HApi.apiPost({
      path: "Aauth2FA",
      name: "UserSetting",
      data: {},
    })
      .then(response => {
        updateSettingByResponse(response)
      })
      .catch(error => {})
  }
  useEffect(() => {
    // console.warn("useEffect:", this)
    requestSetting()
    updateFnUpdateSetting2FAPart(forceUpdate)
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  // console.warn("render: setting2fa")
  return (
    <div>
      <Card>
        <CardHeader
          className="bg-light"
          onClick={() => {
            forceUpdate()
          }}
        >
          <h5 className="mb-0">{t("Two-Factor Authentication")}</h5>
        </CardHeader>
        <CardBody>
          <CardText>{t("setting2fa:settingbox.des")}</CardText>
          <div className="row">
            {Setting2FAHelpers.FA2Methods != null &&
              Setting2FAHelpers.FA2Methods.length > 0 &&
              Setting2FAHelpers.FA2Methods.map((v: any, i) => {
                let _icon = HUtils.get(v, "Configs.Icon")
                let _des = HUtils.get(v, "Configs.Description")
                return (
                  <div className="col-12 col-xl-3 col-md-4 mb-4" key={i}>
                    <Card
                      className={v.IsEnable === true ? "border-1" : ""}
                      style={{ height: "100%" }}
                    >
                      <CardHeader
                        className="p-2 bg-light"
                        style={{
                          color: `${
                            v.IsEnable === true
                              ? "var(--color-success)"
                              : "var(--color-secondary)"
                          }`,
                        }}
                      >
                        <MyIcon
                          data="type:HiBadgeCheck"
                          size={40}
                          style={{
                            position: "absolute",
                            right: "10px",
                            top: "-10px",
                            color: `${
                              v.IsEnable === true
                                ? "var(--color-success)"
                                : "var(--color-secondary)"
                            }`,
                          }}
                        />
                        {_icon && (
                          <div className="d-inline-block">
                            <WrapIcon>
                              <MyIcon data={_icon} />
                            </WrapIcon>
                          </div>
                        )}
                        {v.FA2MethodName}
                      </CardHeader>
                      <CardBody className="p-2 text-dark-25">{_des}</CardBody>
                      <CardFooter className="p-2">
                        <Button
                          key={i}
                          className=""
                          color={v.IsEnable === true ? "secondary" : "primary"}
                          onClick={() => {
                            Setting2FAHelpers.seletedMethod = v
                            HUtils.runFuntion(HConfig, "showGenerate2FA", [v])
                          }}
                        >
                          {v.IsEnable === true ? "Disable" : "Enable"}
                        </Button>
                      </CardFooter>
                    </Card>
                  </div>
                )
              })}
          </div>
        </CardBody>
      </Card>
    </div>
  )
}

export default Part2FA
